import React, {Component} from 'react';
import FadeIn from 'react-fade-in';
import ReactDOM from 'react-dom';
import '../styles/cardDetailsStyle.css';

const domNode = document.getElementById('cardDetailsPopup');
const popup = document.getElementById('cardDetailsPopup');
const rootContainer = document.getElementById('rootContainer');

class CardDetails extends Component {

  handleClose = () => {
    this.props.action();
    popup.style.display = 'none';
    document.querySelector('.activeCard').classList = 'cardDate';
    document.querySelector('body').style.overflow = 'auto';
    //popup.innerHTML = '';
    rootContainer.style.filter = 'blur(0)';
  }

  render() {
    
    let weatherDetails = {
      maxTemp: null,
      minTemp: null,
      humidity: null,
      feelsLike: null,
      pressure: null,
    }

    const {reading} = this.props;

    document.getElementById('cardDetailsPopup').style.display = 'block';
    document.getElementById('rootContainer').style.filter = 'blur(2px)';

    if (reading.dt_txt) {
      weatherDetails.maxTemp = Math.round(reading.main.temp_max) + 2;
      weatherDetails.minTemp = Math.round(reading.main.temp_min);
      weatherDetails.humidity = reading.main.humidity;
      weatherDetails.feelsLike = Math.round(reading.main.feels_like);
      weatherDetails.pressure = reading.main.pressure;
    }

    return ReactDOM.createPortal(
      <FadeIn>
      <div className='weatherDetails'>
        <h6 onClick={this.handleClose} className='close'>X</h6>
        <h1>Max Temp : {weatherDetails.maxTemp} 째C</h1>
        <h1>Min Temp : {weatherDetails.minTemp} 째C</h1>
        <h1>Feels Like : {weatherDetails.feelsLike} 째C</h1>
        <h1>Humidity : {weatherDetails.humidity}</h1>
        <h1>Pressure: {weatherDetails.pressure} mBar</h1>
      </div>
      </FadeIn>,
      domNode
    )
  }
}

export default CardDetails;