import React, {Component} from 'react';
import WeatherContainer from './weatherContainer';
import '../styles/formStyle.css';

var moment = require('moment');
class LocationForm extends Component {

  constructor(props) {
    super(props);

    this.handleWeatherClick = this.handleWeatherClick.bind(this);

    this.inputValue = React.createRef();

    this.state = {
      showWeatherContainer: false,
      city: null,
      cityState: null
    }
  }

  handleWeatherClick = (e) => {
    e.preventDefault();

    var inputValue = this.inputValue.current.value.split(',');

    if(!this.state.showWeatherContainer) {
      this.setState({
        showWeatherContainer: true,
        city: inputValue[0],
        cityState: inputValue[1]

      });
    } else {
      this.setState({
        showWeatherContainer: false
      });
    }
  }

  handleOnChange = () => {
    const weatherContainer = document.querySelector('.weatherContainer');

    if(weatherContainer === null) {
      return;
    } else {
      weatherContainer.style.display = 'none';
      this.setState({
        showWeatherContainer: false
      })
    }
  }

  render() {

    let date = new Date();
    let time = moment(date.getTime()).format('HH');

    return(
      <React.Fragment>
        <div className={(time > 5 && time <= 16)? 'formContainer container' : 'nightFormContainer container'}>
          <div className='row'>
            <form className='col-md-6'>
              <label htmlFor='location'>enter the location</label>
              <input onChange={this.handleOnChange} ref={this.inputValue} id='location' placeholder='(city, state)'></input>
              <button type='submit' onClick={(e) => this.handleWeatherClick(e)}>get weather</button>
            </form>
          </div>
        </div>

        {(this.state.showWeatherContainer) ? <WeatherContainer city={this.state.city} cityState={this.state.cityState} />: ''}
      </React.Fragment>
    )
  }
}

export default LocationForm;
