import React, {Component} from 'react';
import '../styles/backgrounds.css';

var moment = require('moment');
class BackgroundChange extends Component {
  render() {

    const body = document.querySelector('body');
    let date = new Date();
    let time = moment(date.getTime()).format('HH');
  
    time = (time === '00') ? '24' : time;

    if (time >= 5 && time < 12) {
      body.classList = 'morning';
    } else if (time >= 12 && time <= 16) {
      body.classList = 'noon';
    } else if (time > 16 && time <= 17) {
      body.classList = 'evening';
    } else if ((time > 17 && time <= 24) || (time > 0 && time < 5)) {
      body.classList = 'night';
    }



    return(
      <React.Fragment>
        
      </React.Fragment>
    )
  }
}

export default BackgroundChange;