import React, {Component} from 'react';
import CardDetails from './cardDetails';
import '../styles/dayCard.css';

var moment = require('moment');
class DayCard extends Component {

  constructor(props) {
    super(props);

    this.state = {
      showDetails: false,
      currentCardDate: null,
    }

    this.handleShowDetails = this.handleShowDetails.bind(this);
  }

  handleShowDetails = (e) => {

    if(!this.state.showDetails) {
      e.target.parentElement.querySelector('.cardDate').classList = 'activeCard';
      document.querySelector('body').style.overflow = 'hidden';

      this.setState({
        showDetails: true,
        showMobileDetails: true,
        currentCardDate: e.target.parentElement.querySelector('.activeCard').innerHTML
      }); 
    } else {
      e.target.parentElement.querySelector('.activeCard').classList = 'cardDate';
      this.setState({
        showDetails: false
      });
    }
  }

  handleClose = () => {
    this.setState({
      showDetails: false
    })
  }

  render(){

    const reading = this.props.reading;
    let newDate = new Date();
    const weekDay = reading.dt * 1000;
    newDate.setTime(weekDay);
  
    let today = new Date();
    var currentTime = today.getTime();
  
    const imgURL = `owf owf-${reading.weather[0].id} owf-5x`;
    
    return(
      <React.Fragment>
        <div className='eachDay col-sm-2'>
          <div className='card'>
            <h3 className='cardTitle'>{moment(newDate).format('dddd')}</h3>
            <p className='cardDate'> {moment(newDate).format('MMMM Do, ') + moment(currentTime).format('hh:mm a')}</p>
            <i className={imgURL}></i>
            <h2>{Math.round(reading.main.temp)} 째C</h2>
            <div className='cardBody'>
              <p className='cardText'>{reading.weather[0].description}</p>
            </div>
            <button type='button' onClick={(e) => this.handleShowDetails(e)}>See More</button>
          </div>
        </div>

        {(this.state.showDetails) ? <CardDetails reading={reading} action={this.handleClose} /> : ''}
      </React.Fragment>
    );
  }
}

export default DayCard;