import React, {Component} from 'react';
import ApiConfig from './apiKey';
import DayCards from './eachDayCard';
import '../styles/weatherContainer.css';
import moment from 'moment';
import FlipMove from 'react-flip-move';

class WeatherContainer extends Component {

  constructor(props) {
    super(props);

    this.state = {
      fullData: [],
      dailyData: []
    }
  }

  componentDidMount = () => {
    const {city, cityState} = this.props;

    if(city === '' || cityState === '') {
      alert('Please enter location..');
      return;
    }

    const weatherURL = `//api.openweathermap.org/data/2.5/forecast?q=${city},${cityState}&units=metric&appid=${ApiConfig.apiKey}`;

    const date = new Date();
    var currentTime = date.getTime();
    var time24Hr = moment(currentTime).format('HH');

    var currentTimePlus1 = (parseInt(time24Hr) + 1).toString();
    var currentTimePlus2 = (parseInt(time24Hr) + 2).toString();

    currentTimePlus1 = (currentTimePlus1 > 0 && currentTimePlus1 < 10) ? '0' + currentTimePlus1 : currentTimePlus1;
    currentTimePlus2 = (currentTimePlus2 > 0 && currentTimePlus2 < 10) ? '0' + currentTimePlus2 : currentTimePlus2;

    var timeArray = [
      (((time24Hr === '24') ? '00' : time24Hr) + ':00:00'), 
      (((currentTimePlus1 === '24') ? '00': currentTimePlus1) + ':00:00'), 
      (((currentTimePlus2 === '24') ? '00': currentTimePlus2 ) + ':00:00')
    ];

    fetch(weatherURL)
    .then(res => res.json())
    .then(data => {

      if(data.list === undefined) {
        alert('Enter correct location...');
      }

      const dailyData = data.list.filter(reading => timeArray.some(time => reading.dt_txt.includes(time)));
      this.setState({
        fullData: data.list,
        dailyData:dailyData
      });
    });
  } 

  formatDailyCards = () => {
    return this.state.dailyData.map((reading, index) => <DayCards reading={reading} index={index} key={index} />);
  } 

  render() {
    const {city, cityState} = this.props;

    return(
      <div className='weatherContainer container'>
        
        <h5 className='display-5'>{city}, {cityState}</h5>

        <FlipMove className='row justify-content-center' enterAnimation='elevator' staggerDelayBy={150}>
          {this.formatDailyCards()}
        </FlipMove>
      </div>
    )
  }
}

export default WeatherContainer;